.tab-wraper {
    display: flex;
    flex-direction: column;
    height: var(--save-height);
}

.ewe {
    margin: 0 auto;
    padding: 8px;
    padding-bottom: 14px;
    padding-left: max(env(safe-area-inset-left), 8px);
    padding-right: max(env(safe-area-inset-right), 8px);
    @media (max-width: 360px) {
        padding: 0;
    }
}
